<template>
  <div class="login">
    <Navbar />
    <div class="container">
      <div class="row">
        <form class="login-form" @submit.prevent.stop="SignInSubmit">
          <h3 class="mt-3 mb-3">登入</h3>
          <div class="mb-3">
            <label class="form-label">會員帳號</label>
            <input
              type="text"
              class="form-control"
              v-model="username"
              required
            />
          </div>
          <div class="mb-3">
            <label class="form-label">會員密碼</label>
            <input
              type="password"
              v-model="password"
              class="form-control"
              required
            />
          </div>
          <button type="submit" class="btn btn-light">登入</button>
        </form>
        <form class="login-form" @submit.prevent.stop="SignUpSubmit">
          <h3 class="mt-3 mb-3">新用戶</h3>
          <div class="mb-3 ">
            <label class="form-label">帳號名稱</label>
            <input
              type="text"
              v-model="account"
              class="form-control"
              aria-describedby="emailHelp"
              required
            />
          </div>
          <div class="mb-3">
            <label class="form-label">密碼</label>
            <input
              type="password"
              v-model="registPassword"
              class="form-control"
              required
            />
          </div>
          <div class="mb-3">
            <label class="form-label">密碼確認</label>
            <input
              type="password"
              v-model="passwordCheck"
              class="form-control"
              required
            />
          </div>
          <button type="submit" class="btn btn-light">註冊</button>
        </form>
      </div>
    </div>
    <FootNavBar class="mobile-show" />
    <Footer />
  </div>
</template>

<script>
import Footer from "../components/Footer.vue";
import Navbar from "../components/Navbar.vue";
import FootNavBar from "../components/FootNavBar.vue";
import { Toast } from "../utils/helpers";
import { v4 as uuidv4 } from "uuid";

export default {
  components: { Navbar, Footer, FootNavBar },
  name: "UserLogin",
  data() {
    return {
      username: "",
      password: "",
      account: "",
      registPassword: "",
      passwordCheck: "",
      confirmAccount: true,
      confirmPassword: true,
    };
  },
  computed: {
    isLogin: function() {
      return this.$store.getters.getloginState;
    },
  },
  methods: {
    SignInSubmit() {
      let userArray = this.$store.getters.getUserInfo(this.username);
      if (userArray === null) {
        Toast.fire({
          icon: "warning",
          title: "您輸入的帳號有誤",
        });
        this.confirmAccount = false;
        return;
      }
      this.confirmAccount = true;
      if (userArray[0].password == this.password) {
        console.log("login successful");
      } else {
        Toast.fire({
          icon: "warning",
          title: "您輸入的密碼有誤",
        });
        this.confirmPassword = false;
        return;
      }
      this.confirmPassword = true;
      this.$store.commit("changeLoginState");
      this.$store.commit("aboutUser", userArray);
      this.$store.commit("changeUser");
      this.$store.commit("setCurrentUser", userArray);
      Toast.fire({
        icon: "success",
        title: "您已成功登入!",
      });
      this.$router.push("/");
    },
    SignUpSubmit() {
      if (!this.account || !this.registPassword) {
        Toast.fire({
          icon: "warning",
          title: "請填入 帳號 和 密碼",
        });
        return;
      }
      if (this.registPassword !== this.passwordCheck) {
        Toast.fire({
          icon: "warning",
          title: "請輸入相同的密碼",
        });
        return;
      } else {
        this.$store.commit("register", {
          account: this.account,
          password: this.registPassword,
          id: uuidv4(),
        });
        Toast.fire({
          icon: "success",
          title: "加入成功！",
        });
        this.$store.commit("changeUser");
        this.$router.push("/");
      }
    },
  },
};
</script>

